* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

.page {
  min-height: 100vh;
}
.page .content-aria {
  width: calc(100% - 250px);
  min-height: 100vh;
  transition: width 0.3s;
  float: right;
  background-color: #f2f1f6;
}
.page .content-aria.no-sidebar {
  width: 100%;
}

.page .sidebar {
  width: 250px;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  background-color: #292c33;
  color: #fff;
  padding: 1rem;
  transition: left 0.3s;
}
.page .sidebar.no-sidebar {
  left: -250px;
}
.page .sidebar .logo-area {
  font-weight: bold;
  font-size: 1.625rem;
  margin-bottom: 1rem;
}
.page .sidebar .links-area li a {
  display: block;
  position: relative;
  color: #ccc;
  padding: 10px 20px;
  transition: padding 0.3s, color 0.4s;
}
.page .sidebar .links-area li a:hover {
  color: #fff;
}
.page .sidebar .links-area > li > a {
  position: relative;
  color: #fff;
  font-size: 1.15rem;
}
.page .sidebar .links-area > li > a::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  height: 10px;
  width: 10px;
  border-radius: 50%;
  border: 2px solid #fff;
  transform: translateY(-50%);
  transition: background-color 0.3s;
}
.page .sidebar .links-area > li > a:hover {
  padding-left: 25px;
}
.page .sidebar .links-area > li > a:hover::before {
  background-color: #fff;
}
.page .sidebar .links-area .drop-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.page .sidebar .links-area .drop-menu .toggle-submenu {
  transform: rotate(0deg);
  transition: transform 0.3s;
}
.page .sidebar .links-area .drop-menu .toggle-submenu.down {
  transform: rotate(90deg);
}
.page .sidebar .links-area .child-links {
  position: relative;
  max-height: 0;
  overflow: hidden;
  opacity: 0;
  transition: max-height 0.5s ease, opacity 0.5s ease;
}
.page .sidebar .links-area .child-links a {
  position: relative;
}
.page .sidebar .links-area .child-links a::before {
  content: "";
  position: absolute;
  left: 3px;
  top: 0;
  width: 3px;
  height: 100%;
  background-color: #666;
  transition: background-color 0.3s;
}
.page .sidebar .links-area .child-links a:hover::before {
  background-color: #fff;
}
.page .sidebar .links-area .child-links.open {
  max-height: 218px;
  opacity: 1;
}

.header {
  background-color: #fff;
  padding: 1rem;
}
.header .toggle-sidebar {
  cursor: pointer;
}